import { Component } from '@angular/core';
import { NavController, IonicPage, NavParams } from 'ionic-angular';
import { SettingProvider } from '../../providers/setting/setting';
import { UserService, NewsService, CommonService, News } from '../../shared/';
import { AuthPage } from '../auth/auth';
import { ISubscription } from 'rxjs/Subscription';

@IonicPage()
@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage extends AuthPage{

  header_title:any;
  protected newsSubscription : ISubscription ;
  viewloader : any;
  news: News[];

  constructor(public navCtrl: NavController, public navParams: NavParams, protected userService : UserService, protected newsService : NewsService, protected commonService : CommonService) {
    super(navCtrl,navParams,userService)
    this.header_title = SettingProvider.pageSettings.HomePage.pageTitle
  }

  ionViewDidLoad(){
   this.viewloader =  this.commonService.presentLoading(); 
   this.newsSubscription=this.fetchNews().subscribe(
      news => {
        this.commonService.dismissLoading(this.viewloader )
        this.news = news;
      },
      error =>{
        this.commonService.dismissLoading(this.viewloader )
        this.commonService.presentNetWorkRequestError()
      }
    );
  }

  fetchNews(){
    return this.newsService.fetchNews()
  }

  doRefresh(refresher) {
    this.viewloader =  this.commonService.presentLoading();
    this.newsSubscription= this.fetchNews().subscribe(
      news => {
        this.commonService.dismissLoading(this.viewloader )
        this.news=news;
        refresher.complete();
      },
      error =>{
        refresher.complete();
        this.commonService.dismissLoading(this.viewloader )
        this.commonService.presentNetWorkRequestError()
      }
    );
  }

  doInfinite(infiniteScroll) {
    this.newsSubscription= this.fetchNews().subscribe(
      news => {
        this.news=this.news.concat(news);
        infiniteScroll.complete();
      },
      error =>{
        infiniteScroll.complete();
        this.commonService.presentNetWorkRequestError()
      }
    );
  }

  favorite(news){
    this.commonService.presentToast("news is favored");
  }

  attend(news){
    this.commonService.presentToast("we are noticed your attention");
  }

  share(news){
    this.commonService.presentToast("news is shared");
  }

  ionViewCanLeave(){
    //console.log('auth page fun subscribe' );
    super.ionViewCanLeave()
    this.newsSubscription.unsubscribe();
  }
}



