import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Events } from 'ionic-angular';
import { ISubscription } from 'rxjs/Subscription';
import { UserService } from '../../shared/';
import { SettingProvider } from '../../providers/setting/setting';

/**
 * Generated class for the AuthPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// a super page that handle auth and network error..

@IonicPage()
@Component({
  selector: 'page-auth',
  templateUrl: 'auth.html',
})
export class AuthPage {

  protected isAuthenticatedSubscription : ISubscription ;

  constructor(public navCtrl: NavController, public navParams: NavParams, protected userService : UserService) {
  }

  ionViewWillLoad(){
    console.log('view will load call by auth page')
    this.isAuthenticatedSubscription = this.userService.isAuthenticated.subscribe(
      isAuth => {
          if(!isAuth){
            this.isNotAuthenticated();
          }else{
            this.isAuthenticated();
          } 
      },
      error => {
          //console.log('auth page fail to authenticate' + error);
      });
  }


  protected isNotAuthenticated(){
    //this.navCtrl.popToRoot();
    //this.navCtrl.setRoot(SettingProvider.pageSettings.LoginPage.componentName); 
    
  }

  protected isAuthenticated(){
  }


  ionViewCanLeave(){
    //console.log('auth page fun subscribe' );
    this.isAuthenticatedSubscription.unsubscribe();
  }


}



